/* src/components/Galaxy.css */
.galaxy-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Places it behind other content */
  overflow: hidden; /* Ensures the canvas doesn't spill out */
}

/* The actual canvas created by ogl will be a child of this container */
.galaxy-container canvas {
  width: 100% !important;
  height: 100% !important;
}